.footer{width:100%;background-color:#0f0f0f;color:#fff;padding:40px 0;position:relative}.container{max-width:1440px;margin:0 auto;padding:0 20px}.footer-top{display:flex;justify-content:space-between;gap:30px;margin-bottom:30px}@media(max-width: 991px){.footer-top{flex-direction:column}}.footer-intro{flex:2}@media(max-width: 991px){.footer-intro{flex:1}}.footer-title{font-size:20px;font-weight:600;margin-bottom:15px}.footer-text{font-size:14px;line-height:1.5;margin-bottom:20px;color:#ccc;max-width:400px}.feedback-btn{display:inline-flex;align-items:center;background-color:#3b5acf;color:#fff;font-weight:600;font-size:14px;padding:10px 24px;border-radius:30px;text-decoration:none;transition:all .2s ease}.feedback-btn:hover{background-color:#2c4bc0}.arrow{margin-left:8px}.footer-contact{flex:2;display:flex;flex-direction:column;gap:20px}@media(max-width: 991px){.footer-contact{flex:1}}.contact-item{display:flex;align-items:flex-start;gap:10px}.contact-icon{margin-top:4px}.contact-info{display:flex;flex-direction:column}.contact-label{font-size:12px;color:#ccc;margin-bottom:4px}.contact-value{font-size:14px;font-weight:600;color:#fff}.contact-sub{font-size:12px;color:#ccc;margin-top:2px}.footer-social{flex:1;display:flex;gap:15px}@media(max-width: 991px){.footer-social{flex:1}}.social-link{display:flex;align-items:center;justify-content:center;transition:transform .2s ease}.social-link:hover{transform:translateY(-3px)}.social-icon{width:40px;height:40px;-o-object-fit:contain;object-fit:contain}.footer-divider{height:1px;background-color:#333;margin:30px 0}.desktop-links{display:grid;grid-template-columns:repeat(5, 1fr);gap:20px;margin-bottom:40px}@media(max-width: 991px){.desktop-links{display:none}.footer-links-column:last-child{grid-column:1/-1}}.column-title{font-size:16px;font-weight:600;margin-bottom:20px;color:#fff}.second-title{margin-top:30px}.links-list{list-style:none;padding:0;margin:0}.links-list li{margin-bottom:10px}.links-list li a{font-size:14px;color:#ccc;text-decoration:none;transition:color .2s ease;line-height:1.5}.links-list li a:hover{color:#fff}.address-list li{margin-bottom:15px}.address-list li a{font-size:13px}.footer-links-mobile{display:none;flex-direction:column;margin-bottom:40px}@media(max-width: 991px){.footer-links-mobile{display:flex}}.accordion-item{border-bottom:1px solid #333}.accordion-item:last-child{border-bottom:none}.accordion-item.active .toggle-icon{transform:rotate(45deg)}.accordion-item.active .accordion-content{display:block;padding-bottom:15px}.accordion-header{display:flex;justify-content:space-between;align-items:center;padding:15px 0;cursor:pointer}.accordion-header .column-title{margin-bottom:0}.toggle-icon{font-size:24px;font-weight:300;color:#fff;transition:transform .2s ease}.accordion-content{display:none}.footer-bottom{display:flex;justify-content:space-between;align-items:center;gap:20px}@media(max-width: 991px){.footer-bottom{flex-direction:column;align-items:flex-start}}.company-info{flex:3}.copyright{font-size:14px;font-weight:600;margin-bottom:8px}.registration{font-size:12px;color:#ccc;line-height:1.5;max-width:600px}.certificates{display:flex;gap:15px;justify-content:flex-end}@media(max-width: 991px){.certificates{justify-content:flex-start}}.cert-link{transition:opacity .2s ease}.cert-link:hover{opacity:.8}.cert-link img{max-height:40px}.back-to-top{position:fixed;bottom:20px;right:20px;z-index:100}.back-top-link{display:flex;align-items:center;justify-content:center;width:40px;height:40px;background-color:#3b5acf;border-radius:50%;transition:transform .2s ease}.back-top-link:hover{transform:translateY(-3px)}